.container {
  display: flex;
  justify-content: center;
  padding: 40px 20px;
  gap: 30px;
  background: var(--bg-color);
  min-height: 100vh;
  flex: 1;
}

.main-content {
  width: 65%;
  background: var(--card-bg);
  border-radius: 15px;
  padding: 20px;
  box-shadow: var(--shadow);
  color: var(--text-color);
  font-family: 'Poppins', sans-serif;
}

.main-content h1 {
  font-size: 2.2rem;
  margin-bottom: 15px;
  color: var(--text-color);
  font-weight: 600;
}

.mode-divider {
  border: 0;
  height: 2px;
  background: var(--text-color);
}

[data-theme="light"] .mode-divider {
  background: #d35400;
}

[data-theme="dark"] .mode-divider {
  background: #fcd46c;
}

.projects-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
  justify-content: space-between;
}

.project-card {
  background: #333333;
  padding: 15px;
  border-radius: 10px;
  flex: 1 1 calc(33.33% - 20px);
  max-width: calc(33.33% - 20px);
  color: #e0e0e0;
  box-sizing: border-box;
  min-width: 0;
}

[data-theme="light"] .project-card {
  background: #f0f0f0;
  color: #1a1a1a;
}

.project-card h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: #ffffff;
}

[data-theme="light"] .project-card h3 {
  color: #000000; /* Black for project titles like "Budget Tracker" in light mode */
}

.project-card p {
  font-size: 1rem;
  line-height: 1.5;
}

.project-card p strong {
  color: #ffffff;
}

[data-theme="light"] .project-card p strong {
  color: #000000; /* Black for "Technologies:" in light mode */
}

.highlight {
  color: #d35400;
}

[data-theme="light"] .highlight {
  color: #e67e22;
}

[data-theme="dark"] .highlight {
  color: #fcd46c;
}

/* Ensure special text remains readable */
[data-theme="light"] .main-content p,
[data-theme="light"] .main-content p strong {
  color: #1a1a1a;
}

@media (max-width: 768px) {
  .container {
    flex-direction: column;
    padding: 15px 10px;
    gap: 15px;
  }
  .main-content {
    width: 100%;
    padding: 15px;
  }
  .main-content h1 {
    font-size: 1.8rem;
  }
  .projects-grid {
    flex-direction: row;
    justify-content: center;
    gap: 10px;
  }
  .project-card {
    flex: 1 1 calc(90% - 10px);
    max-width: 90%;
    padding: 10px;
    margin: 0 5px;
  }
  .project-card h3 {
    font-size: 1.3rem;
  }
  .project-card p {
    font-size: 0.9rem;
    line-height: 1.4;
  }
}

@media (max-width: 480px) {
  .project-card {
    flex: 1 1 calc(100% - 10px);
    max-width: 100%;
    margin: 0;
  }
}